html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #111111;
  font-size: 1.6;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  color: #013448;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  text-align: center;
}

h1 {
  font-size: 5.4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
  font-weight: 700;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 2rem;
}

.boton-azul {
  background-color: #013448;
  font-size: 2rem;
  color: white;
  border-radius: 5rem;
  padding: 1rem 2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.boton-azul:hover {
  cursor: pointer;
}

.boton-amarillo {
  background-color: #CC9702;
  font-size: 2rem;
  color: black;
  border-radius: 5rem;
  padding: 1rem 2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
.boton-amarillo:hover {
  cursor: pointer;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.hero {
  display: flex;
  flex-direction: column;
  background-image: url(../img/pexels-rodnae-productions-7821913.webp);
  background-size: cover;
  background-position: center;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: space-evenly;
}
.hero h1, .hero h2 {
  text-transform: uppercase;
  padding: 0;
  margin: 1rem;
}

.header {
  background-color: #CC9702;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 1rem;
}
.alerta.error {
  background-color: #CC9702;
}
.alerta.exito {
  background-color: #013448;
  color: white;
}

.clientes {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
.clientes thead {
  background-color: #013448;
}
.clientes thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.clientes td {
  padding: 0.5rem 0;
  text-align: center;
}
.clientes .eliminar {
  display: inline-block;
}

.formulario {
  margin: 3rem 0 2rem 0;
  font-size: 1.6rem;
  color: #333333;
}
.formulario legend {
  font-size: 2rem;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 0.5rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #333333;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario p {
  margin: 2rem 0 0 0;
}
.formulario p:first-of-type {
  margin: 0;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}